<template>
	<view class="background fix-padding">
		<!-- 新建信息按钮 -->
		<view class="container add">
			<navigator url="/pages/budget/add/add">
				<button class="flex" type="default">
					<image class="image" src="/static/image/add.png"></image>
					<view>新增预算单</view>
				</button>
			</navigator>
		</view>
		<!-- 搜索 -->
		<view class="container search">
			<view class="flex">
				<uni-easyinput class="input" v-model="search_name" placeholder="请输入客户名称"></uni-easyinput>
				<button class="btn-search" @click="search">搜索</button>
			</view>
			<!-- <view class="flex mt-20">
				<picker class="picker" style="width: 100%;text-align: left;" @change="changeSale" :value="sIndex" :range="Sale_list" :range-key="'username'">
					<view style="width: 600rpx;" v-if="sIndex == -1" class="uni-input">选择销售</view>
					<view style="width: 600rpx;" v-else class="uni-input">{{Sale_list[sIndex].username}}</view>
					<image class="image" src="/static/image/picker.png"></image>
				</picker>
			</view> -->
		</view>
		<!-- 列表 -->
		<view class="container list">
			<view class="item" v-for="(item,index) in list" :key="item.id">
				<view class="big ellipsis">
					预算单号{{item.budget_number}}
				</view>
				<view class="flex top .mb-10">
					<view class="title grey">客户名称</view>
					<view class="value">{{item.name}}</view>
				</view>
				<view class="flex top .mb-10">
					<view class="title grey">销售员</view>
					<view class="value">{{item.username}}</view>
				</view>
				<view class="flex .mb-10 last">
					<view class="title grey">备注内容</view>
					<view class="value">{{item.remarks}}</view>
				</view>
				<view class="arrow arrow flex flex-column" @click="editor(item.id)">
					<view>编辑</view>
				</view>
				<view class="arrow arrow flex flex-column" style="right: 140rpx; border-color: #FF5722; color: #FF5722;" @click="details(item.id)">
					<view>详情</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				search_name:'',
				list:[],
				page:0,
				sIndex:-1,
				Sale_list:[],
				create_user_id:'',
			}
		},
		onLoad() {
			this.page = 0
			// 销售
			this.http({
				url: this.api.getUserData,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					
				},
				success: res => {
					this.Sale_list = res
					let item = {
						id:'',
						username:"全部",
					}
					this.Sale_list.unshift(item)
				}
			})
			// 出库记录接口
			this.listInterface()
		},
		//上拉触底函数
		onReachBottom(){
			// 列表接口
			this.http({
				url: this.api.budget_index,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					search: this.search_name,
					page:this.page,
					create_user_id:this.create_user_id,
				},
				success: res => {
					this.page+=1
					if(res.list.length == 0) {
						uni.showToast({
							title:'没有更多了',
							icon:'none',
							duration: 2000
						})
					} else {
						this.list = this.list.concat(res.list)
					}
				}, 
			})
		},
		methods: {
			// 销售点击
			changeSale(e) {
				this.page = 0
				this.sIndex = e.target.value
				this.create_user_id = this.Sale_list[this.sIndex].id
				this.listInterface()
			},
			// 列表接口
			listInterface() {
				this.http({
					url: this.api.budget_index,
					method: "POST",
					data: {
						uid: this.common.get("uid"),
						search:this.search_name,
						page:this.page,
						create_user_id:this.create_user_id,
					},
					success: res => {
						this.page+=1
						if(res.list.length == 0) {
							uni.showToast({
								title:'没有数据',
								icon:'none',
								duration: 2000
							})
							this.list = res.list
						} else {
							this.list = res.list
						}
					}
				})
			},
			// 搜索 查询按钮点击
			search() {
				this.page = 0
				this.listInterface()
			},
			// 时间
			searchTime() {
				this.page = 0
				this.listInterface()
			},
			// 详情按钮点击
			details(eve){
				uni.navigateTo({
					url:'/pages/budget/details/details?data='+encodeURIComponent(JSON.stringify(eve))
				})
			},
			// 编辑按钮点击
			editor(eve){
				uni.navigateTo({
					url:'/pages/budget/editor/editor?data='+encodeURIComponent(JSON.stringify(eve))
				})
			},
			
		}
	}
</script>

<style lang="scss">
.arrow_text {
	position: absolute;
	right: 0;
	top: 30rpx;
	view{
		font-size: 20rpx;
		font-weight: 400;
		padding: 12rpx;
		border-radius: 32rpx 0px 0px 32rpx;
	}
}
.btn-color {
	width: 120rpx!important;
	height: 64rpx;
	line-height: 64rpx;
	background: $color-b5;
}
</style>
